import React, { useState } from 'react';

const infrastructureData = [
    { id: 1, title: "State-of-the-Art Campus", image: "/images/gallery-images/campus2.webp" },
    { id: 2, title: "Classrooms & Learning Spaces", image: "/images/gallery-images/class-room.webp" },
    { id: 3, title: "Library & Research Center", image: "/images/gallery-images/library.webp" },
    { id: 4, title: "Computer Labs & IT Facilities", image: "/images/gallery-images/computer-lab.webp" },
    { id: 5, title: "Hostel & Accommodation", image: "/images/gallery-images/campus1.webp" },
    { id: 6, title: "Sports & Recreation", image: "/images/gallery-images/sports.webp" },
    { id: 7, title: "Cafeteria & Dining", image: "/images/gallery-images/dining.webp" },
    { id: 8, title: "Transport & Connectivity", image: "/images/gallery-images/transport.webp" },
    { id: 9, title: "Yoga & Meditation", image: "/images/gallery-images/yoga.webp" },
];

export default function Infrastructure() {
    const [selectedImage, setSelectedImage] = useState(null);

  return (
    <div>
        <div className="container-fluid px-0 overflow-hidden page-banner" style={{ background: "url(/images/infrastructure-bnr.webp)" }}>
            <nav aria-label="breadcrumb">
            <ol class="breadcrumb">
                <li class="breadcrumb-item">Discover</li>
                <li class="breadcrumb-item active" aria-current="page">
                <strong>Infrastructure</strong>
                </li>
            </ol>
            </nav>
        </div>

        <section className='py-5'>
        <div className='container'>
          <div className='one'>
            <h1 className='text-primary mb-3 text-center'>Vishwa Vishwani Infrastructure</h1>
          </div>
          <div className='row'>
            {infrastructureData.map((item) => (
              <div key={item.id} className='col-lg-4 mb-4'>
                <div className='infrastructure-card' onClick={() => setSelectedImage(item.image)}>
                  <img src={item.image} className='w-100' alt={item.title} />
                  <h4 className='text-center p-2 m-0 fs-5 text-primary'>{item.title}</h4>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Image Popup */}
      {selectedImage && (
        <div className="image-popup" onClick={() => setSelectedImage(null)}>
          <div className="popup-content">
            <img src={selectedImage} alt="Selected" />
          </div>
        </div>
      )}

    </div>
  );
}
