export default function DVV() {
  return (
    <div>
      <div className="container-fluid px-0 overflow-hidden page-banner">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item">AQAR</li>
            <li class="breadcrumb-item active" aria-current="page">
              <strong>Vishwa Vishwani</strong>
            </li>
          </ol>
        </nav>
      </div>

      <section className='py-5'>
        <div className='container'>
            <div className="row">
                <div className="col one text-center">
                    <h1 className="text-primary display-4 mb-4">DVV Metrics - 1</h1>
                </div>
            </div>
            <div className='row'>
                <div className='col-lg-12'>
                    <div className='dvv-links'>
                        <table className="table table-bordered text-center align-middle">
                            <thead>
                            <tr className="table-primary">
                                <th>Title</th>
                                <th>Link</th>
                            </tr>
                            </thead>
                            <tbody>
                            {linksData.map((item, index) => (
                                <tr key={index}>
                                <td className="text-start">{item.text}</td>
                                <td>
                                    <a href={item.link} className="btn btn-primary" target="_blank" rel="noopener noreferrer">
                                        View Document
                                    </a>
                                </td>
                                </tr>
                            ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>

            <div className="row">
                <div className="col one text-center">
                    <h1 className="text-primary display-4 mb-4 mt-5">DVV Metrics - 2</h1>
                </div>
            </div>
            <div className='row'>
                <div className='col-lg-12'>
                    <div className='dvv-links'>
                        <table className="table table-bordered text-center align-middle">
                            <thead>
                            <tr className="table-primary">
                                <th>Title</th>
                                <th>Link</th>
                            </tr>
                            </thead>
                            <tbody>
                                {additionalLinksData.map((item, index) => (
                                    <tr key={index} className={item.title ? "table-info" : ""}>
                                        <td className="text-start">
                                            {item.title ? <strong>{item.title}</strong> : item.text}
                                        </td>
                                        <td>
                                        {item.link && (
                                            <a
                                            href={item.link}
                                            className="btn btn-primary"
                                            target="_blank"
                                            rel="noopener noreferrer"
                                            >
                                            View Document
                                            </a>
                                        )}
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
      </section>
    </div>
  );
}
