import React from "react";
import { NavLink } from "react-bootstrap";

export default function Admissions_process() {
  return (
    <div>
      <div
        className="container-fluid px-0 overflow-hidden page-banner"
        style={{ background: "url(/images/admission_process.webp)" }}
      >
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item">Admissions</li>
            <li class="breadcrumb-item active" aria-current="page">
              <strong>Process</strong>
            </li>
          </ol>
        </nav>
      </div>

      <section className="py-5">
        <div className="container">
          <div className="row one text-center">
            <h1 className="text-primary">Admission Process</h1>
            <p className="mb-5">
              The admissions process usually starts with engaging with students
              to clarify course requirements and provide information about the
              desired program. Following this, candidates will need to submit
              their applications either online or offline for the chosen course.
              Shortlisted candidates will be invited based on their performance
              in the aptitude test. Subsequently, selection will occur based on
              merit during the interview. After this, an offer letter will be
              issued to the chosen candidates. Lastly, the candidates must pay
              the admission fee to confirm their enrollment.
            </p>
            <img src="/images/admissions-flow.webp" className="w-100" />
          </div>
        </div>
      </section>

      <section className="py-5 bg-grey">
        <div className="container">
          <div className="row">
            <div className='col-lg-6 three px-4 mb-lg-0 mb-3'>
              <div className="card">
                <div className="card-body">
                  <h1 className="fs-1 text-primary fw-bold">Eligibility BBA</h1>
                  <div className="text-start three brochure-box">
                    <ul>
                      <li><i class="fa-solid fa-caret-right"></i> 10+2 or Intermediate from any State or Central board, passed with minimum 50% aggregate from</li>
                      <li><i class="fa-solid fa-caret-right"></i> Valid Score/Rank/Percentile in JEE/EAPCET/NEET/CLAT/IPMAT/ UGAT or any other equivalent entrance exam conducted by any State Government body.</li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
            <div className='col-lg-6 three px-4'>
              <div className="card">
                <div className="card-body">
                  <h1 className="fs-1 text-primary fw-bold">Eligibility MBA</h1>
                  <div className="text-start three brochure-box">
                    <ul>
                      <li><i class="fa-solid fa-caret-right"></i> Any Graduation with minimum 50% aggregate from any UGC recognized University or Institution</li>
                      <li><i class="fa-solid fa-caret-right"></i> Valid Score/Rank/Percentile in CAT/MAT/ATMA/XAT/CMAT/GMAT/ ICET or any other equivalent entrance exam conducted by any State Government body.</li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className='py-5'>
        <div className="container">
          <div className="row">
            <div className="col-lg-12 d-flex justify-content-center">
              <h4 className="mb-4 text-primary roboto-slab">Grab the Opportunity... Limited Seats <span className="pulse-text">Available</span></h4>
            </div>
            <div className="col-lg-12 d-flex justify-content-center">
              <NavLink className="btn btn-primary py-2 px-4 text-white rounded-pill shadow"> Apply Now</NavLink>
            </div>
          </div>
        </div>
      </section>

    </div>
  );
}
