import React from "react";

export default function StudentsClubs() {
  return (
    <div>
      <div
        className="container-fluid px-0 overflow-hidden page-banner"
        style={{ background: "url(/images/student-clubs-bg.webp)" }}
      >
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb">
            <li class="breadcrumb-item">Discover</li>
            <li class="breadcrumb-item active" aria-current="page">
              <strong>Students Clubs</strong>
            </li>
          </ol>
        </nav>
      </div>

      <section className="welcome py-3 py-lg-5">
        <div className="container py-3">
          <div className="row d-flex align-items-center justify-content-center">
            <div className="col-lg-7">
              <div className="text-start three">
                <h1 className="display-6 text-primary">
                  <strong className="fw-bold">Students Clubs</strong>
                </h1>
                <p>
                  Student-led clubs at GNITS enhance learning through
                  interaction and discussion, offering students opportunities to
                  showcase and groom their talents. Every activity carried out
                  by these clubs encourages students to cultivate different
                  perceptions of life, shaping their interests, hobbies,
                  leadership, and social skills. GNITS aims to provide holistic
                  education by balancing academics with extracurricular
                  activities, enhancing students’ personalities, and developing
                  both hard and soft skills. The clubs help students build small
                  communities based on shared interests such as dance, music,
                  science, arts, community service, or photography. Through
                  these activities, students cultivate a sense of unity, achieve
                  common goals, and develop social skills by interacting with
                  peers from diverse backgrounds. Club initiatives encourage
                  students to step out of their comfort zones, discover their
                  inner potential, and increase their confidence and
                  independence.
                </p>
              </div>
            </div>
            <div className="col-lg-5">
              <img
                src="/images/clubs.webp"
                className="img-fluid rounded"
                alt="academic"
              />
            </div>
          </div>
        </div>
      </section>

      <section className="py-5 bg-grey">
        <div className="container">
          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture1.jpg" className="w-100 mb-lg-0 mb-3" />
              </div>
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">
                  Athlete club / Sports Club
                </h2>
                <h4 className="text-primary">
                  Founded : <b>2019</b>
                </h4>
                <p>
                  The objective of this club is to promote physical fitness,
                  teamwork, and inclusivity through diverse sports and
                  recreational activities. It aims to foster discipline,
                  sportsmanship, and a sense of community by bringing people
                  together from various backgrounds.
                </p>
                <a href="#" className="btn btn-primary">
                  View Details
                </a>
              </div>
            </div>
          </div>

          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">
                  Research and development
                </h2>
                <h4 className="text-primary">
                  Founded : <b>2021</b>
                </h4>
                <p>
                  Objective of this club is promoting Research Culture, Skill
                  Development, Innovation and Creativity, Collaborative
                  Learning, Mentorship and Guidance.
                </p>
                <a href="#" className="btn btn-primary mb-lg-0 mb-3">
                  View Details
                </a>
              </div>
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture2.jpg" className="w-100" />
              </div>
            </div>
          </div>

          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture3.jpg" className="w-100 mb-lg-0 mb-3" />
              </div>
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">Cinematic club</h2>
                <h4 className="text-primary">
                  Founded : <b>2021</b>
                </h4>
                <p>
                  Cinematic club of Vishwa Vishwani Started in 2023 with the
                  objective to use films as a tool for learning, inspiration,
                  and critical thinking. The club aims to analyze cinematic
                  works to explore leadership, entrepreneurship, ethics,
                  teamwork, and innovation.
                </p>
                <a href="#" className="btn btn-primary">
                  View Details
                </a>
              </div>
            </div>
          </div>

          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">
                  Persona Development
                </h2>
                <h4 className="text-primary">
                  Founded : <b>2022</b>
                </h4>
                <p>
                  The Persona Development Club (Established in 2022) The Persona
                  Development Club is dedicated to unlocking students' hidden
                  potential and equipping them with essential 21st-century
                  skills such as creativity, confidence, communication, critical
                  thinking, problem-solving, and emotional intelligence. This
                  club serves as a dynamic platform for students to enhance
                  their interpersonal skills and leadership qualities through
                  workshops, interactive sessions, and group discussions. With a
                  strong focus on self-awareness and resilience, the club
                  empowers students to proactively tackle personal and
                  professional challenges.
                </p>
                <a href="#" className="btn btn-primary mb-lg-0 mb-3">
                  View Details
                </a>
              </div>
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture4.jpg" className="w-100" />
              </div>
            </div>
          </div>

          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture5.jpg" className="w-100 mb-lg-0 mb-3" />
              </div>
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">
                  Social- environmental club
                </h2>
                <h4 className="text-primary">
                  Founded : <b>2023</b>
                </h4>
                <p>
                  The Environmental Awareness Club established in the year 2023
                  aims to promote understanding of critical environmental issues
                  such as climate change, deforestation, pollution, and
                  biodiversity loss. It organizes activities focused on tackling
                  challenges like education, poverty, and health through the
                  lens of environmental sustainability.
                </p>
                <a href="#" className="btn btn-primary">
                  View Details
                </a>
              </div>
            </div>
          </div>

          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">
                  Entrepreneurship club
                </h2>
                <h4 className="text-primary">
                  Founded : <b>2023</b>
                </h4>
                <p>
                  The objective of the Entrepreneurship Club is to foster a
                  culture of innovation and entrepreneurial thinking among
                  management students. This club aims to inspire and support
                  aspiring entrepreneurs by providing a platform to explore
                  business ideas, develop critical skills, and gain practical
                  knowledge of starting and managing ventures.
                </p>
                <a href="#" className="btn btn-primary mb-lg-0 mb-3">
                  View Details
                </a>
              </div>
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture6.jpg" className="w-100" />
              </div>
            </div>
          </div>

          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture7.jpg" className="w-100 mb-lg-0 mb-3" />
              </div>
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">Cultural and art club</h2>
                <h4 className="text-primary">
                  Founded : <b>2021</b>
                </h4>
                <p>
                  This Cultural club promote awareness, creativity, and
                  self-expression while preserving heritage and celebrating
                  festivals. They foster community engagement, leadership, and
                  teamwork, supporting social causes and providing platforms for
                  talent. By encouraging collaboration and intercultural
                  dialogue, they strengthen partnerships, showcasing diverse
                  traditions and uniting people through shared cultural
                  experiences.
                </p>
                <a href="#" className="btn btn-primary">
                  View Details
                </a>
              </div>
            </div>
          </div>

          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">National service scheme</h2>
                <h4 className="text-primary">
                  Founded : <b>2023</b>
                </h4>
                <p>
                  This club aims to foster personality development, social
                  service, and national integration while nurturing leadership,
                  community engagement, and employability skills. Emphasizing
                  social responsibility, empathy, teamwork, and innovation, it
                  cultivates life skills and fosters a sense of duty toward
                  society, shaping well- rounded individuals committed to
                  positive change and national unity.
                </p>
                <a href="#" className="btn btn-primary mb-lg-0 mb-3">
                  View Details
                </a>
              </div>
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture8.jpg" className="w-100" />
              </div>
            </div>
          </div>

          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture9.jpg" className="w-100 mb-lg-0 mb-3" />
              </div>
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">Finesto club</h2>
                <h4 className="text-primary">
                  Founded : <b>2023</b>
                </h4>
                <p>
                  This club focuses on enhancing students' financial knowledge
                  and practical skills through education, hands-on experience,
                  and industry exposure, preparing them for careers in finance,
                  investment, and business.
                </p>
                <a href="#" className="btn btn-primary">
                  View Details
                </a>
              </div>
            </div>
          </div>

          <div className="clubs-box">
            <div className="row d-flex align-items-center justify-content-center">
              <div className="col-lg-9">
                <h2 className="text-primary roboto-slab">Literary club</h2>
                <h4 className="text-primary">
                  Founded : <b>2023</b>
                </h4>
                <p>
                  A literary club fosters a love for literature, poetry, and
                  diverse literary forms while encouraging creative expression
                  through writing, storytelling, and public speaking. It
                  enhances critical thinking through discussions and debates,
                  promotes a strong reading culture, and provides a platform for
                  intellectual discourse, enriching minds through meaningful
                  literary engagement.
                </p>
                <a href="#" className="btn btn-primary mb-lg-0 mb-3">
                  View Details
                </a>
              </div>
              <div className="col-lg-3 col-8">
                <img src="/images/clubs-img/Picture10.jpg" className="w-100" />
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}
